.heroworks{
    display: flex;
    height: 300lvh;
    width: 100vw;
    justify-content: center;
}
.works-wrapper{
    display: flex;
    height: 100%;
    width: 95%;
    /* background-color: slateblue; */
    position: relative;
    clip-path: inset(0);
}
.works-top{
    display: flex;
    position: absolute;
    flex-direction: column;
    top: 0;
    /* border: 5px solid darkred; */
    height: 100lvh;
    width: 100%;
    z-index: 2;
    justify-content: center;
    align-items: center;
}
.works-bottom{
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: space-evenly;
    top: 0;
    /* border: 10px solid darkgreen; */
    height: 100lvh;
    width: 100%;
    z-index: 1;
    transform: scale(1.3) rotateX(45deg) rotateY(-8deg) rotateZ(27deg) translateX(0px) translateY(0px) translateZ(0px) skewX(0deg) skewY(0deg);
}
.works-row{
    height: 30%;
    width: 120%;
    display: flex;
    justify-content: space-evenly;

}
.row-1{
    transform: translateX(-5rem);
}
.row-2{
    transform: translateX(-30rem);
}
.row-3{
    transform: translateX(-5rem);
}
.work-canvas{
    width: 30vw;
    height: 100%;
    background-color: rgba(255, 218, 185, 0.497);
    opacity: 0.3;
}

